/* eslint-disable */
import React from "react";
import styled from "styled-components";

import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { actionCreators as userActions } from "../redux/modules/user";
import {emailCheck, passwordCheck, phoneCheck, nickCheck} from "../shared/common";

import loginPage from '../assets/img/loginPage.png'
import logo from '../assets/img/.svg'

const Signup = () => {
  const history = useHistory();
  const dispatch = useDispatch();

  const [email, setEmail] = React.useState("");
  const [userName, setUserName] = React.useState("");
  const [userPw, setUserPw] = React.useState("");
  const [userPwCheck, setUserPwCheck] = React.useState("");
  const [phoneNumber, setPhoneNumber] = React.useState("");

  const signup = () => {
    if (email === "" || userName === "" || userPw === "" || userPwCheck === "" || phoneNumber === "") {
      window.alert("  ");
      return;
    }
    //  
    if (userPw !== userPwCheck) {
      window.alert("  !");
      return;
    }
    // 
    if (!emailCheck(email)) {
      window.alert("   !");
      return;
    }
    // 
    if (!nickCheck(userName)) {
      window.alert(" , ,   2-10 !");
      return;
    }
    // 
    if (!passwordCheck(userPw)) {
      window.alert("     6~12  !");
      return;
    }
    //  
    if (!phoneCheck(phoneNumber)) {
      window.alert(" 000-0000-0000    ");
      return;
    }
    dispatch(
      userActions.signupDB(email, userName, userPw, userPwCheck, phoneNumber)
    );
  };

  return (
    <PageMain>
      <InputMain>
        <img src={logo} style={{position : 'absolute',top : '32px',left : '30px', cursor : 'pointer'}} onClick={() => {history.push('/')}}/>
        <SingUpContentsWrapper>
          <MainContents></MainContents>
          <SubContents>      .</SubContents>
          <UserInput
            type="text"
            value={email}
            placeholder=" "
            onChange={(e) => {setEmail(e.target.value)}}
          />
          <UserInput
            type="text"
            value={userName}
            placeholder=" (2  7 )"
            onChange={(e) => {setUserName(e.target.value)}}
            maxLength="7"
          />
          <UserInput
            type="password"
            value={userPw}
            placeholder="(  6~12)"
            onChange={(e) => {setUserPw(e.target.value)}}
          />
          <UserInput
            type="password"
            value={userPwCheck}
            placeholder=" "
            onChange={(e) => {setUserPwCheck(e.target.value)}}
          />
          <UserInput
            type="text"
            value={phoneNumber}
            placeholder=" (- )"
            onChange={(e) => {setPhoneNumber(e.target.value)}}
          />
          <CollectionBtn onClick={signup}></CollectionBtn>
        </SingUpContentsWrapper>
      </InputMain>
      <LogoMain src = {loginPage} />
    </PageMain>
    );
};

const PageMain = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width : 1440px;
  min-height : 812px;
`;

const PageInput = styled.form`
  display: flex;
`;

const LogoMain = styled.img`
  width: 50%;
  height: 100%;
`;

const InputMain = styled.div`
width: 50%;
height: 100%;
text-align: center;
display: flex;
`;

const SingUpContentsWrapper = styled.div`
  width: 380px;
  height: 500px;
  margin: auto;
  padding : 0 30px;
    @media screen and (min-width: 2560px) {
      width: 700px;
      height: 820px;
    }
`

const MainContents = styled.div`
  font-weight: 700;
  font-size: 24px;
  margin : 0 0 48px;
    @media screen and (min-width: 2560px) {
      font-size: 38px;
      margin: 0 125px 40px;
    }
`;

const SubContents = styled.div`
  font-weight: 400;
  font-size: 16px;
    @media screen and (min-width: 2560px) {
      font-size: 24px;
      margin: 0 95px 40px;
    }
`;

const CollectionBtn = styled.div`
  width: 384px;
  height: 40px;
  border-radius: 10px;
  background-color: #1F3A5E;
  font-weight: 700;
  font-size: 16px;
  color: white;
  text-align : center;
  line-height: 40px;
  display : inline-block;
  cursor: pointer;
  margin-top : 16px;
    @media screen and (min-width: 2560px) {
      width: 580px;
      height: 60px;
      font-size: 24px;
      margin : 8px 60px;
      line-height: 60px;
    }
`;

const UserInput = styled.input`
  width: 372px;
  height : 40px;
  font-weight: 400;
  font-size: 16px;
  border: 1px solid #828282;
  border-radius: 10px;
  margin-top: 16px;
  padding-left : 8px; 
  &:hover {
    border: 1px solid #282828;
  }
  &:focus {
    outline: 1px solid #282828;
    border: -1px solid white;
  }
    @media screen and (min-width: 2560px) {
      width: 564px;
      height: 60px;
      font-size: 24px;
      margin : 8px 60px;
    }
`;

export default Signup;


